@import url("https://use.typekit.net/jmk3xov.css");

canvas {
  position: fixed;
  left:0;
      top:0;
  z-index:-1;
  
}
@font-face {
  font-family: "Courier";
  src: url("./public/font/CourierPrime-Regular.ttf")
  format("ttf");
  font-style: normal;
  font-weight: 400;
}
main {
  font-family: "Courier";
  width: 100vw;
  color: white;
  z-index: 99;
  position: absolute;
  width: 100%;
  margin: 0px auto;
  padding: 120px 0px;
  
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  
}

header {
  font-family: "Courier";
  border-radius: 25px;
  border: 2px solid #000000;
  background: var(--dark-bg);
  grid-column: 2 / span 6;
  font-size: 2.5vw;
  padding: 2vw;
  margin-bottom:50px;
  display: flex;
  flex-direction: column;
 
}


section {
  border-radius: 25px;
  grid-column: 2 / 8;
  padding: 0.75rem;
  background: rgba(48, 162, 214, 0.897);
  font-size: 1.75vw;
  line-height: 1.5;
  margin-bottom: 175px;
  margin-top: -150px;
  display: flex;
  flex-direction: column;
}
:root {
  --dark-bg: rgba(15, 15, 15, 1);
  --spacing: 350px;
  background: var(--dark-bg);
  font-family: "Courier";
  font-weight: 400;
  font-style: normal;
  overflow-x: hidden;
  
}

blockquote {
  margin: 0;
  padding: 0.25px;
  grid-column: 2 / span 8;
  
  margin-bottom: 150px;
  display: flex;
  margin-top: -50px;
}

img {
  border-radius: 5px;
}

blockquote p {
  color: black;
  border-radius: 25px;
  padding: 10px;
  border: 2px solid #000000;
  background-color: rgba(252, 203, 6, 0.637);

  font-size: 2.5vw;
  display: flex;
  
  line-height: 1;
  
}

.left {
  grid-column: 6 / 12;
}

.test {
  
  border-radius: 25px;
  grid-column: 2 / 8;
  padding: 0;
  background: rgba(177, 221, 241, 0);
  font-size: 1.75vw;
  line-height: 1.5;
  margin-bottom: 175px;
  margin-top: -150px;
  display: flex;
  flex-direction: column;
}
.firstblq {
  margin-bottom: 75px;
}

@media screen and (max-width: 820px) {
  section {
    justify-content: center;
    font-size: 1.0rem;
    grid-column: 1/13;
    margin-left: -20px;
    margin-bottom: 150px;
    border-radius: 0px;
    border: 2px solid #000000;
  }
  .left {
    justify-content: center;
    font-size: 1.0rem;
    grid-column: 1/13;
    margin-left: -20px;
  }
  :root {
    overflow-x: hidden;
  }
  canvas {
    overflow-x: hidden;
  }

  main {
    margin: auto auto;
  }
  
  header {
    grid-column: 3 / span 8;
    justify-content: center;
    padding: 3rem;
  }
  .test {
  
    justify-content: center;
    font-size: 1.0rem;
    grid-column: 1/13;
    margin-left: -20px;
  }
  blockquote {
    grid-column: 3 / span 8;
    justify-content: center;
  }
  
}